import React from "react";
export const Buttons = props => {
    const genName = props.name || `${props.id ? `id${props.id}` : ''}${props.spawn ? `spawn${props.spawn}` : ''}` || "default";
    return (React.createElement("div", { className: "buttons" },
        props.showFn && (React.createElement("button", { className: "button primary", onClick: props.showFn, "data-test-id": `button-show-${genName}` }, `Show ${genName}`)),
        props.hideFn && (React.createElement("button", { className: "button", onClick: props.hideFn, "data-test-id": `button-hide-${genName}` }, `Hide ${genName}`))));
};
